const styles = theme => ({
  root: {
    position: 'relative',
    display: 'flex',
    flexDirection: 'column',
    flex: 1,
    margin: 0,
    padding: 0
  },
  enterOverlay: {
    zIndex: 1000,
    position: 'absolute',
    left: 0,
    right: 0,
    top: 0,
    bottom: 0,
    display: 'flex',
    padding: 20,
    backgroundColor: '#000000'
  },
  enterElement: {
    margin: 'auto',
    fontFamily: 'monospace',

    '& button': {
      fontFamily: 'monospace'
    }
  },
  '@global': {
    '*': {
      cursor: 'none !important'
    },
    'html, body': {
      cursor: 'none !important'
    },
    'a, button, input, select, textarea': {
      cursor: 'none !important'
    },
    '[role="button"]': {
      cursor: 'none !important'
    },
    '.clickable': {
      cursor: 'none !important'
    },
    // Override any potential third-party cursor styles
    '*:hover': {
      cursor: 'none !important'
    },
    // Ensure cursor stays hidden even during interactions
    '*:active': {
      cursor: 'none !important'
    },
    '*:focus': {
      cursor: 'none !important'
    },
    // Add hover effects for interactive elements
    'a, button, .clickable, [role="button"]': {
      '&:hover': {
        '& ~ div[class*="cursor"]': {
          transform: 'scale(1.5)',
          backgroundColor: `${theme.color.secondary.main}22`,
          mixBlendMode: 'difference'
        }
      }
    },
    'input, textarea, select': {
      '&:hover ~ div[class*="cursor"]': {
        transform: 'scale(0.8)',
        backgroundColor: `${theme.color.secondary.main}44`
      }
    }
  },
  cursor: {
    position: 'fixed',
    width: 20,
    height: 20,
    border: `2px solid ${theme.color.secondary.main}`,
    borderRadius: '50%',
    pointerEvents: 'none',
    zIndex: 99999,
    transform: 'translate(-100px, -100px)',
    transition: 'all 0.15s ease-out, opacity 0.3s ease-in-out',
    mixBlendMode: 'difference',
    willChange: 'transform',
    backfaceVisibility: 'hidden',
    '&::after': {
      content: '""',
      position: 'absolute',
      top: '50%',
      left: '50%',
      width: 4,
      height: 4,
      backgroundColor: theme.color.secondary.main,
      borderRadius: '50%',
      transform: 'translate(-50%, -50%)',
      transition: 'all 0.15s ease-out'
    },
    '&.hovering': {
      transform: 'scale(1.5)',
      backgroundColor: `${theme.color.secondary.main}22`,
      '&::after': {
        transform: 'translate(-50%, -50%) scale(1.2)'
      }
    }
  },
  cursorDot: {
    position: 'fixed',
    width: 4,
    height: 4,
    backgroundColor: theme.color.secondary.main,
    borderRadius: '50%',
    pointerEvents: 'none',
    zIndex: 99999,
    transform: 'translate(-100px, -100px)',
    transition: 'width 0.2s, height 0.2s, opacity 0.3s ease-in-out',
    mixBlendMode: 'difference',
    willChange: 'transform',
    backfaceVisibility: 'hidden',
    '&.hovering': {
      width: 8,
      height: 8
    }
  },
  '@media (max-width: 480px)': {  // Mobile
    enterOverlay: {
      padding: 10
    },
    enterElement: {
      width: '100%',
      maxWidth: 300
    }
  },
  '@media (min-width: 481px) and (max-width: 768px)': {  // Tablet
    enterOverlay: {
      padding: 15
    },
    enterElement: {
      maxWidth: 400
    }
  },
  '@media (min-width: 769px) and (max-width: 1024px)': {  // Small laptop
    enterElement: {
      maxWidth: 500
    }
  },
  '@media (min-width: 1025px) and (max-width: 1200px)': {  // Desktop
    enterElement: {
      maxWidth: 600
    }
  },
  '@media (min-width: 1201px)': {  // Large screens
    enterElement: {
      maxWidth: 700
    }
  }
});

export { styles };
