import React from 'react';
import hoistNonReactStatics from 'hoist-non-react-statics';
import { createUseStyles, ThemeProvider } from 'react-jss';
import theme from '../settings/theme';

const withStyles = styles => Component => {
  // Create the style function with theme
  const useStyles = createUseStyles(styles, {
    name: Component.displayName || Component.name || 'Component'
  });

  // Create a wrapper component that will inject the theme and styles
  const WithStylesWrapper = React.forwardRef((props, ref) => {
    const classes = useStyles(props);
    
    return (
      <ThemeProvider theme={theme}>
        <Component {...props} classes={classes} ref={ref} theme={theme} />
      </ThemeProvider>
    );
  });

  WithStylesWrapper.displayName = `WithStyles(${Component.displayName || Component.name || 'Component'})`;
  hoistNonReactStatics(WithStylesWrapper, Component);

  return WithStylesWrapper;
};

export { withStyles }; 