import { SCHEME_EXPAND } from './Menu.constants';

const styles = theme => ({
  root: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
    margin: '0 auto',
    userSelect: 'none',
    minWidth: '600px',
    width: '100%',
    maxWidth: '800px',
  },
  item: {
    display: 'block',
    padding: '0 10px',
    whiteSpace: 'nowrap',
    lineHeight: 1,
    fontSize: 14,
    textAlign: 'center',
    textTransform: 'uppercase',
    textShadow: `0 0 5px ${theme.color.secondary.main}`,
    fontFamily: theme.typography.primary,
    color: theme.color.text.main
  },
  divisor: {
    display: 'none',
    width: 0,
    color: theme.color.tertiary.main,
    textShadow: `0 0 5px ${theme.color.tertiary.main}`,
    fontWeight: 'normal',
    transform: 'scale(1, 0)',
    transformOrigin: 'center center'
  },
  link: {
    overflow: 'hidden',
    opacity: ({ scheme }) => scheme === SCHEME_EXPAND ? 0 : 1,

    '&.link-active': {
      color: theme.color.tertiary.main,
      textShadow: `0 0 5px ${theme.color.tertiary.main}`
    },
    '&:hover, &:focus': {
      color: theme.color.secondary.light,
      textShadow: `0 0 5px ${theme.color.secondary.light}`
    }
  },

  '@media (min-width: 768px)': {
    item: {
      display: 'block'
    },
    divisor: {
      display: 'block'
    }
  },

  '@media (max-width: 480px)': {
    root: {
      minWidth: '100%',
      flexDirection: 'column',
      gap: 10
    },
    item: {
      fontSize: 12,
      padding: [5, 8]
    },
    divisor: {
      display: 'none'
    }
  },

  '@media (min-width: 481px) and (max-width: 768px)': {
    root: {
      minWidth: '90%',
      maxWidth: '100%'
    },
    item: {
      fontSize: 13,
      padding: [5, 10]
    }
  },

  '@media (min-width: 769px) and (max-width: 1024px)': {
    root: {
      minWidth: '500px'
    }
  }
});

export { styles };
